import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import CMap from "../components/CMap";
import Logout from "../components/Logout";
import {
  logOut,
  connectSocket,
  listenTo,
  getMarkers,
  joinRoom
} from "../actions";
import Series from "../components/Series";
import Hunt from "../components/Hunt";

const SERIES = "SERIES";
const MAP = "MAP";

const Creator = ({
  connected,
  connectToSocket,
  joinSeries,
  logout,
  mapKey,
  markers,
  socket,
  getMarkers,
  listenToNewMarker
}) => {
  const [scene, setScene] = useState(SERIES);
  const [selectedSeries, setSelectedSeries] = useState();

  useEffect(() => {
    if (!connected) {
      connectToSocket();
    }
    // if (!selectedSeries) return;
    // getMarkers(selectedSeries);
    // joinSeries(selectedSeries);
    // listenToNewMarker();
    //eslint-disable-next-line
  }, [connectToSocket]);
  return (
    <div>
      {selectedSeries}
      <Logout logout={logout} />
      {scene === SERIES && (
        <div>
          <Hunt />
          <Series setScene={setScene} setSelectedSeries={setSelectedSeries} />
        </div>
      )}
      {scene === MAP && (
        <CMap
          mapKey={mapKey}
          markers={markers}
          series={selectedSeries}
          setScene={setScene}
          getMarkers={getMarkers}
          socket={socket}
        />
      )}
    </div>
  );
};

const mapStateToProps = ({
  map: { mapKey, markers },
  socket: { connected, socket }
}) => ({
  connected,
  mapKey,
  markers,
  socket
});
const mapDipstachToProps = dispatch => ({
  connectToSocket: () => dispatch(connectSocket()),
  getMarkers: series => dispatch(getMarkers(series)),
  joinSeries: series => dispatch(joinRoom(series)),
  listenToNewMarker: () => dispatch(listenTo("new_marker")),
  logout: () => dispatch(logOut()),
  dispatch
});

export default connect(mapStateToProps, mapDipstachToProps)(Creator);
